<template>
  <div id="content">
    <div id="pathDiv">
      <div>
        <el-input v-model="path" readonly="true"></el-input>
      </div>
      <div>
        <el-button type="primary" plain @click="parent_path">Parent Folder</el-button>
      </div>
    </div>

    <el-dialog title="" :visible.sync="showImg" width="60%">
      <img :src="curImgUrl" alt="">
    </el-dialog>
    <div v-if="isImgFolder">
      <el-row :gutter="20">
        <el-col :span="6" v-bind:key="index" v-for="(item, index) in imgCurList"
                style="margin-bottom: 20px">
          <el-card :body-style="{ padding: '10px', cursor: 'pointer', height: '100px', lineHeight: '150px'}"
                   @click.native="click_item(item)">
            <img :src="item.smallurl" alt="">
          </el-card>
        </el-col>
      </el-row>
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="imgPageNo"
        :page-sizes="[20, 40, 60, 100]"
        :page-size="imgPageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="imgCount">
      </el-pagination>
    </div>
    <div v-else>
      <el-row :gutter="20">
        <el-col :span="6" v-bind:key="index" v-for="(item, index) in dirList"
                style="margin-bottom: 20px">
          <el-card
            :body-style="{ padding: '20px', cursor: 'pointer'}"
            @click.native="click_item(item)">
            <el-tooltip v-if="item.dir_name.length > 20"
              class="item" effect="dark" :content="item.dir_name" placement="top-end">
              <div>
                <span class="dib"><i class="el-icon-goods"></i></span>
                <span class="file_name dib">{{item.dir_name}}</span>
              </div>
            </el-tooltip>
            <div v-else>
              <span class="dib"><i class="el-icon-goods"></i></span>
              <span class="file_name dib">{{item.dir_name}}</span>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import {Bus} from '../event-bus'

export default {
  name: 'Content',
  data () {
    return {
      path: '',
      isImgFolder: true,
      dirList: [],
      imgList: [],
      imgCurList: [],
      imgPageNo: 1,
      imgPageSize: 20,
      imgCount: 0,
      curImgUrl: '',
      showImg: false
    }
  },
  created () {
    Bus.$on('Dataset', this.load_dataset)
    Bus.$on('Project', this.load_project)
    Bus.$on('Command', this.load_command)
    this.load_path()
  },
  methods: {
    load_dataset () {
      this.path = 'dataset'
      this.load_path()
    },
    load_project () {
      this.path = 'project'
      this.load_path()
    },
    load_command () {
      this.path = 'command'
      alert('unavailable now!')
      this.imgList = []
      this.dirList = []
    },
    click_item (item) {
      if (item.type === 'image') {
        this.curImgUrl = item.url
        this.showImg = true
        return
      }
      this.path = item.path
      this.load_path()
    },
    parent_path () {
      this.path = this.path.split('/')
      if (this.path.length > 1) {
        this.path.pop()
      }
      this.path = this.path.join('/')
      this.load_path()
    },
    handleCurrentChange (pageNo) {
      this.imgPageNo = pageNo
      let start = (this.imgPageNo - 1) * this.imgPageSize
      this.imgCurList = this.imgList.slice(start, start + this.imgPageSize)
    },
    handleSizeChange (pageSzie) {
      this.imgPageSize = pageSzie
      this.handleCurrentChange(1)
    },
    load_path () {
      this.$axios.get('/api/path?dir=' + this.path)
        .then(response => {
          let data = response.data
          this.isImgFolder = data.is_img_folder
          if (this.isImgFolder) {
            this.imgList = data.items
            this.imgCount = this.imgList.length
            this.handleCurrentChange(1)
          } else {
            this.dirList = data.items
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #content {
    display: inline-block;
    width: 1000px;
    margin: 20px;
    padding: 30px;
    background-color: RGBA(102, 153, 255, 0.1);
  }
  #pathDiv {
    height: 80px;
  }
  #pathDiv div {
    display: inline-block;
  }
  #pathDiv div:nth-child(1) {
    float: left;
    width: 800px;
  }
  #pathDiv div:nth-child(2) {
    float: right;
    width: 120px;
  }
  er_col {
    margin: 10px;
  }
  .dib {
    display: inline-block;
    vertical-align: top;
  }
  .file_name {
    text-overflow: ellipsis;
    overflow: hidden;
    max-width: 170px;
    white-space: nowrap ;
  }
  img{
    max-width:600px;
  }
</style>



